* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
html {
  width: 100%;
  height: 100%;
  font-size: 100%;
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 16px;
  font-size: 1em /* 16px */;
  line-height: 1.25 /* 20px */;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: #333;
}
.page-wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}
.empty {
  height: 100px;
}
.cf:before,
.row:before,
.cf:after,
.row:after {
  display: table;
  content: " ";
  clear: both;
}
.container {
  max-width: 1800px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
  overflow: hidden;
}
.row {
  margin: 0 -15px;
}
template {
  display: none;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: rgba(0,0,0,0.25);
  z-index: 1;
}
.gallery {
  background-color: #343d46;
  background-image: -moz-linear-gradient(left, #343d46 0%, #4f5b66 25%, #65737e 50%, #a7adba 75%, #c0c5ce 100%);
  background-image: -webkit-linear-gradient(left, #343d46 0%, #4f5b66 25%, #65737e 50%, #a7adba 75%, #c0c5ce 100%);
  background-image: linear-gradient(to right, #343d46 0%, #4f5b66 25%, #65737e 50%, #a7adba 75%, #c0c5ce 100%);
  min-height: 100vh;
}
.gallery__btn {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  width: 0;
  height: 0;
  position: fixed;
  top: 0;
  left: 0;
  margin-left: -9999px;
}
.gallery__btn-label {
  margin: 0;
  padding: 0;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 140px;
  left: 15px;
  background-color: rgba(0,0,0,0.063);
  border: 1px dotted rgba(255,255,255,0.25);
  box-shadow: 0 0 48px 1px rgba(255,255,255,0.25);
  border-radius: 8px;
}
.gallery__btn-label span {
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 2px;
  margin-left: -10px;
  margin-top: -1px;
  background-color: rgba(255,255,255,0.375);
  transition: 0.25s ease all;
}
.gallery__btn-label span:before {
  content: " ";
  display: block;
  position: absolute;
  top: -7px;
  width: 20px;
  height: 2px;
  background-color: rgba(255,255,255,0.375);
  transition: 0.25s ease all;
}
.gallery__btn-label span:after {
  content: " ";
  display: block;
  position: absolute;
  top: 7px;
  width: 20px;
  height: 2px;
  background-color: rgba(255,255,255,0.375);
  transition: 0.25s ease all;
}
.gallery__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 15px 0;
  transition: 0.25s ease padding-left;
}
.gallery__item {
  margin: 0;
  padding: 0;
  float: left;
  width: 25%;
}
.gallery__item-inner {
  margin: 15px;
  padding-top: 56.25%;
  position: relative;
  overflow: hidden;
}
.gallery__link {
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.gallery__img {
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
  transition: 0.25s ease max-width, 0.25s ease max-height;
}
.gallery__img_portrait {
  max-width: 100%;
}
.gallery__img_landscape {
  max-height: 100%;
}
.gallery__img_on_error {
  max-width: 100%;
  max-height: 100%;
}
.gallery__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(52,61,70,0.5);
  transition: 0.25s ease all;
  opacity: 0;
}
.gallery__title {
  margin: 0;
  padding: 0;
  font-size: 48px;
  text-align: center;
  text-transform: uppercase;
  color: rgba(255,255,255,0.75);
  margin-top: 120px;
}
.gallery__overlay-title {
  margin: 0;
  padding: 0;
  font-size: 32px;
  color: rgba(255,255,255,0.75);
  text-transform: uppercase;
  text-align: center;
  display: inline-block;
  vertical-align: top;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.filter {
  position: absolute;
  top: 210px;
  left: 0;
  bottom: 0;
  width: 25%;
  margin-left: -25%;
  background-color: rgba(52,61,70,0.5);
  padding: 15px;
  overflow: hidden;
  transition: 0.25s ease margin-left;
}
.filter__title {
  margin: 0;
  padding: 0;
  color: rgba(255,255,255,0.75);
  text-align: center;
  text-transform: uppercase;
  font-size: 32px;
}
.filter__btn {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  display: block;
  width: 100%;
  padding: 15px;
  font-size: 20px;
  background-color: rgba(0,0,0,0.063);
  border: 1px dotted rgba(255,255,255,0.25);
  box-shadow: 0 0 48px 1px rgba(255,255,255,0.25);
  margin-top: 30px;
  color: rgba(255,255,255,0.75);
  transition: 0.25s ease background-color, 0.25s ease border, 0.25s ease box-shadow, 0.25s ease color, 0.25s ease text-shadow;
  border-radius: 8px;
}
.filter__btn_selected {
  background-color: rgba(0,0,0,0.375);
  border: 1px solid rgba(255,255,255,0.25);
  box-shadow: 0 0 48px 1px rgba(255,255,255,0.5);
  color: #fff;
  text-shadow: 0 0 16px rgba(255,255,255,0.5);
}
.gallery__link:hover ~ .gallery__overlay {
  opacity: 1;
}
.gallery__link:hover ~ .gallery__img_portrait {
  max-width: 120%;
}
.gallery__link:hover ~ .gallery__img_landscape {
  max-height: 120%;
}
.gallery__btn:checked ~ .gallery__list {
  padding-left: 25%;
}
.gallery__btn:checked ~ .gallery__list .gallery__item {
  width: 33.3333333333%;
}
.gallery__btn:checked ~ .filter {
  margin-left: 0;
}
.gallery__btn:checked ~ .gallery__btn-label span {
  background-color: transparent;
}
.gallery__btn:checked ~ .gallery__btn-label span:before {
  transform: rotate(45deg);
  top: 0;
}
.gallery__btn:checked ~ .gallery__btn-label span:after {
  transform: rotate(-45deg);
  top: 0;
}
.footer {
  position: relative;
  height: 100px;
  margin-top: -100px;
  background-color: rgba(0,0,0,0.75);
}
